//import VueClipboard from 'vue-clipboard2';
import http from './js/http.js'
import protocol from "./js/protocol"
import backConst from "./js/backConst";
import myUtil from "./js/util";
import { baseUrl,loadUrl } from './js/env';

/*Vue.use(VueAxios, axios)*/
//Vue.use(VueClipboard)
Vue.prototype.$axios=axios
Vue.use(VueRouter)
//Vue.use(Vuex)
Vue.use(iview)
global.baseUrl=baseUrl
global.loadUrl=loadUrl
global.http = http;
global.protocol = protocol;
global.myUtil = myUtil;
global.backConst = backConst;
const router = new VueRouter({
    routes
})

router.beforeEach((to, from, next) => {
    if (to.path == '/login') {
        sessionStorage.removeItem('user');
    }
  let user = JSON.parse(sessionStorage.getItem('user'));

    //判断登陆账号是否是嗖嗖身边  临时
/*  if (to.path == '/chart'&&user!='sousoushenbian') {
    sessionStorage.removeItem('user');
    next({ path: '/login' })
    iview.Message.info('请登陆');
  }
  if (to.path == '/echartChange'&&user!='sousoushenbian') {
    sessionStorage.removeItem('user');
    next({ path: '/login' })
    iview.Message.info('请登陆');
  }
  if (to.path == '/echartTICK'&&user!='sousoushenbian') {
    sessionStorage.removeItem('user');
    next({ path: '/login' })
    iview.Message.info('请登陆');
  }*/
  //当账号是嗖嗖时，限制他的跳转  只能跳转两个页面
  if((user=='sousoushenbian'&&to.path != '/chart'&&to.path != '/echartChange')){
    sessionStorage.removeItem('user');
    next({ path: '/login' })
    iview.Message.info('请登陆');
  }

    if (!user && to.path != '/login') {
        next({ path: '/login' })
    } else {
        next()
    }
})

//router.afterEach(transition => {
//NProgress.done();
//});

new Vue({
    el: '#app',
    template: '<App/>',
    router,
   // store,
    components: { App }
    //render: h => h(Login)
}).$mount('#app')

