<template>
  <div style="height:100%;" ref="tableWrap">
    <div class="search-wrap" ref="searchWrap">
      <Row :gutter="16">
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">操作类型：</span>
            <Select v-model="operationType" class="input-width">
              <Option v-for="item in statueList" :value="item.value" :key="item.value" class="input-width">{{ item.label }}</Option>
            </Select>
          </div>
        </Col>
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">操作内容：</span>
            <Input v-model="operationContent" placeholder="操作内容" class="input-width"></Input>
          </div>
        </Col>
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">操作人：</span>
            <Input v-model="operator" placeholder="操作人" class="input-width"></Input>
          </div>
        </Col>
        <Col span="8">
          <div  class="input-item">
            <span class="input-label">操作时间：</span>
            <DatePicker type="daterange" @on-change="getOperationTime"  placement="bottom-start" placeholder="操作时间" class="input-width"></DatePicker>
          </div>
        </Col>
        <Col span="2">
          <div  class="input-item">
            <Button type="primary" @click="searchInfo">查询</Button>
          </div>
        </Col>
      </Row>
    </div>
    <!--操作按钮区域-->
    <div class="handle-wrap" ref="handleWrap">
      <div class="handle-btn-wrap">
        <Button type="primary" @click="exportInfo">导出</Button>
      </div>
    </div>
    <!--table表格区域-->
    <Table :loading="loading" :height="tableHeight" :data="tableData" :columns="tableColumns" ref="table" stripe border></Table>
    <div style="margin-top:10px;height:45px;" ref="pageWrap">
      <div style="float: right;">
        <Page :total="total" :current="page" @on-change="changePage" show-total></Page>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: "OperationLogQuery",//操作日志
    data () {
      return {
        /*搜索区域 条件*/
        operationContent:'', //操作内容
        operator:'',//操作人
        operationTime:'', //操作时间
        operationType:'',//操作类型
        statueList:[
          {
            label: '已委托',
            value: '11'
          },
          {
            label: '已成交',
            value: '111'
          },
          {
            label: '已撤单',
            value: '1111'
          },
        ],
        /*table表格*/
        tableData: [],
        total: 0,
        page: 1,
        size: 10,//条数
        tableHeight:200,
        loading:true,
        tableColumns: [
          {
            title: '操作类型',
            key: 'jumpway',
            align: 'center',
            sortable: true,
            minWidth: 100,
          },
          {
            title: '操作内容',
            align: 'center',
            key: 'jumpway',
            sortable: true,
            minWidth: 100,
          },
          {
            title: '操作人',
            align: 'center',
            key: 'jumpway',
            minWidth: 100,
          },
          {
            title: '操作时间',
            key: 'jumpway',
            align: 'center',
            minWidth: 100,
          }
        ]
      }
    },
    mounted() {
      this.tableHeight=this.$refs.tableWrap.clientHeight-50
      this.loadData();
    },
    methods: {
      //暂时请求推荐商家
      loadData() {
        var param = new Object();
        param["query_type"] = "Pages";
        param["pPage"] = this.page;
        param["pSize"] = this.size;
        this.$Loading.start();
        this.loading=true;

        let paramData = protocol.getJsonParams(protocol.RecommendMerchant, param);
        http.ajax('', paramData, true, (data) => {

          this.loading=false;
          this.$Loading.finish();
          this.tableData = data.CooperationSeller;
          this.total = data.total;
        })
      },
      //分页查询
      changePage (row) {
        this.page = row;
        this.loadData();
      },
      //获取操作时间
      getOperationTime(e){
        this.operationTime=e
        console.log(e)
      },
      //查询
      searchInfo () {
        //默认：查询结果为空、操作时间为当前自然日

      },
      //导出
      exportInfo () {
        var form = document.createElement('form')
        form.enctype = 'application/x-www-form-urlencoded'
        form.method = 'POST'
        form.action = baseUrl + '/pc/information/exportInfo.do'
        var map = {
          'token': that.token,
          'strApp': that.strApp,
          'strInfoNo': that.strInfoNo, //
          'isCash': that.isCash, // 全部   是 否
          'strContent': that.strContent, // 内容
        }

        for (var i in map) {
          var f = document.createElement('input')
          f.type = 'hidden'
          f.name = i
          f.value = map[i]
          form.appendChild(f)
        }
        document.body.appendChild(form)
        form.submit()
        document.body.removeChild(form)
        form = null
        // this.$refs.table.exportCsv({
        //   filename: '积分绑定查询数据'
        // });
      },
      exportCancel(){

      },
      //导出数据
      exportInfo(){
        let that=this
        this.$Modal.confirm({
          title: '提示',
          content: `确定导出吗？`,
          onOk: function(){
            that.exportOk()
          }
        })
      },
    }
  }
</script>

<style lang="less" scoped>
  @import '../../../css/common.css';
</style>





