//http://10.100.11.27:8080/AS   http://172.16.20.102:81/PS
function showToast(text) {
  iview.Message.info(text);
}

function showLoading() {
  iview.LoadingBar.start();
   //this.$Loading.finish();
}

function hideLoading() {
  iview.LoadingBar.finish();
}

export default {

  ajax(url, data, loading, success,fail) {
    console.log("请求参数:\n" + data);
    //如果需要菊花则显示
    if (loading) {
      showLoading();
    }
    let endUrl='';//根据传参的不同，设置不同的访问地址 （middle 代表中间层）
    if(url=='middle'){
      endUrl=middleUrl+"/PS"
    }else{
      endUrl=baseUrl+"/PS" + url
    }
    let self = this;
    let myVue=new Vue()
    return myVue.$axios({
      method: "POST",
      // url: "http://42.81.112.45/PS" + url,
      //url: "http://10.100.20.231/PS" + url,
      //url: "http://172.20.66.11:81/PS" + url,
      url: endUrl,
      //url: "http://172.16.20.200/PS" ,
      contentType: "application/json",
      dataType: "json",
      withCredentials: false,
      data: data,
      timeout: 30000,
    }).then(function (res) {
      hideLoading();
      console.log("返回数据,成功:\n" + JSON.stringify(res.data));
      let data=res.data
      if (data.return.head.result === 'failure') {
        if(fail){
          fail()
        }
        var errCode = data.return.head.error_code;
        if (errCode === '0010016' || errCode === '0010017' || errCode === '0010018' || errCode === '0010019' || errCode === '0010020') {
          //token无效
          backConst.clearUserInfo();
          //实名认证、注册并登录、登录、注销登录、自动退出登录
          eventBus.$emit('refreshPoint', '');

          showToast("登录过期,请重新登录");
          setTimeout(() => {
            self.goRoute('/login');
          }, 1500);
        } else {
          showToast(data.return.head.error_reason);
          if (data.return.head.error_code === '0020004') {
            //用户已注册
            success("0020004");
          } else if (data.return.head.error_code === '0010004') {
            //用户未注册
            success("0010004");
          }
        }
      } else {
        //token可用,返回数据
        success(data.return.data);
      }
    }).catch(function (err) {
      if(fail){
        fail()
      }
      console.log("返回数据,失败:\n" + JSON.stringify(err));
      showToast("网络异常");
      hideLoading();
    });
  },
}


